import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { PaymentDetailsComponent } from './payment-details.component';
import { PendingPaymentsComponent } from './pending-payments/pending-payments.component';
import { AuthGuard } from '../auth/auth.guard';
import { WeeklyDisbursementsComponent } from './weekly-disbursements/weekly-disbursements.component';


const routes: Routes = [
    
    {path:'',component: PaymentDetailsComponent,canActivate:[AuthGuard]},
    {path:'viewpayments',component: PendingPaymentsComponent,canActivate:[AuthGuard]},
    {path:'weeklyDisbursements',component: WeeklyDisbursementsComponent,canActivate:[AuthGuard]},
   
];
@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class PaymentRouting {}
